<script>
  import Todo from "./components/TodoComponent.svelte";
  import ProgressBar from "./components/ProgressComponent.svelte";
  import Radio from "./components/RadioComponent.svelte";

  let todo = [
    { id: "fw4e1ql20titpsqlccbbx", content: "Задача 1", priority: "high" },
    { id: "lkxvbm81y1iau1ry9i7ik", content: "Задача 2", priority: "medium" },
    { id: "lkxvbm81y1iau1ry9i", content: "Задача 3", priority: "low" }
  ];

  let content = "";
  let priority = "";

  function submitHandler() {
    todo = [...todo, {
      id: Date.now(),
      content: content,
      priority: priority
    }];

    content = "";
    priority = "";
  }
</script>

