  :root {
    --todo-box-bg: #f7f1fb;
    --btn-bg: #b6b6ef;
  }

  h2 {
    margin: 0 0 30px;
    text-align: center;
  }

  .todo-box {
    margin: 25px 0 0;
    padding: 20px 50px 15px;
    background-color: var(--todo-box-bg);
    border-radius: 15px;
    box-shadow: 5px 10px 15px rgba(0,0,0, .25);
  }

  input[type="text"] {
    display: block;
    width: 100%;
    padding: 10px 14px;
    color: #3d3c70;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    border-radius: 25px;
    border: none;
    background-color: #e7e2f4;
    outline: none;
  }

  input[type="text"]:not(:last-child) {
    margin: 0 0 15px;
  }

  button {
    display: block;
    margin: 30px auto 0;
    padding: 10px 25px;
    color: #3d3c70;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    border: none;
    border-radius: 50px;
    background-color: var(--btn-bg);
    cursor: pointer;
    box-shadow: 5px 10px 15px rgba(0,0,0, .25);
    outline: none;
  }
  
  button:hover {
    background-color: #9f9fea;
  }

  button:active {
    box-shadow: 2px 4px 15px rgba(0,0,0, .25);
  }

  .todo-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
</style>

<div class="container">
  <div class="todo-box">
    <h2>Мои задачи</h2>

    <form class="todo-form">
      <input type="text" placeholder="Введите название" bind:value={content}>

      <Radio />

      <button type="submit" on:click|preventDefault={submitHandler}>добавить новую</button>
    </form>

    <ul class="todo-list">
      {#each todo as {content, priority}}
        <Todo content={content} priority={priority} />
      {/each}
    </ul>

    <ProgressBar />
  </div>
</div>
