<script>
  export let select = '';
</script>

<style>
  .radio-box {
    display: flex;
    justify-content: space-between;
  }

  .radio-box__item {
    display: inline-block;
    width: 30%;
  }

  input[type="radio"] {
    display: none;
  }

  label {
    display: block;
    padding: 10px 14px;
    color: #3d3c70;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    text-align: center;
    border-radius: 25px;
    border: none;
    background-color: #e7e2f4;
    cursor: pointer;
  }

  input[type="radio"]:checked + label {
    background: #b6b6ef;
  }
</style>

<div class="radio-box">
  <div class="radio-box__item">
    <input type=radio bind:group={select} id="low" name="priority" value="low">
    <label for="low">Низний</label>
  </div>

  <div class="radio-box__item">
    <input type=radio bind:group={select} id="medium" name="priority" value="medium">
    <label for="medium">Средний</label>
  </div>

  <div class="radio-box__item">
    <input type=radio bind:group={select} id="high" name="priority" value=high>
    <label for="high">Высокий</label>
  </div>
</div>
